.user-contacts {
  margin-bottom: 12px;
}

.user-contacts__list {
  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.user-contacts__item {
  position: relative;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    margin-bottom: 23px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
  }
}

.user-contacts__item .text-label {
  padding-left: 22px;

  @media (min-width: @desktop-width) {
    padding-left: 0;
    padding-top: 26px;
    padding-right: 15px;
  }
}

.user-contacts__item .text-input {
  margin-top: 9px;

  @media (min-width: @desktop-width) {
    width: 315px;
  }
}

.user-contacts__telephone-icon,
.user-contacts__mail-icon {
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 100;
    width: 58px;
    height: 58px;
    background-color: @icon-gray;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.user-contacts__telephone-icon {
  &::after {
    background-image: url("../img/icon-phone.svg");

    @media (min-width: @desktop-width) {
      right: 80px;
    }
  }
}

.user-contacts__mail-icon {
  &::after {
    background-image: url("../img/icon-mail.svg");
  }
}
