  font-size: 21px;
  line-height: 26px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  color: #ffffff;
  box-sizing: border-box;
  background-color: #81B3D2;
  border: none;
}
.button:hover {
  background-color: #669ec0;
}
.button:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}
.button-disabled {
  background-color: #e5e5e5;
}
.button-disabled:hover,
.button-disabled:focus {
  background-color: #e5e5e5;
}
.text-input,
.text-input::placeholder {
  font-weight: bold;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  color: #000000;
  text-align: left;
  text-transform: none;
  opacity: 1;
}
.text-input {
  box-sizing: border-box;
  width: 100%;
  height: 58px;
  padding-left: 17px;
  padding-top: 5px;
  background-color: #F2F2F2;
  border: 4px solid transparent;
}
.text-input:hover {
  background-color: #e5e5e5;
}
.text-input:focus {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
}
.option-input--radio + label::before,
.option-input--checkbox + label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 56px;
  height: 56px;
  background-color: #F2F2F2;
}
.option-input--radio + label::before {
  border-radius: 50%;
}
@media (min-width: 1200px) {
  .option-input--radio + label::before {
    left: -74px;
  }
}
.option-input--radio:checked + label::after {
  content: "";
  position: absolute;
  top: 19px;
  left: 17px;
  width: 22px;
  height: 22px;
  background-color: #333333;
  border-radius: 50%;
}
@media (min-width: 1200px) {
  .option-input--radio:checked + label::after {
    left: -56px;
  }
}
.option-input--checkbox + label::before {
  top: 1px;
  left: -1px;
  border-radius: 10%;
}
@media (min-width: 768px) {
  .option-input--checkbox + label::before {
    left: -5px;
  }
}
@media (min-width: 1200px) {
  .option-input--checkbox + label::before {
    left: 1px;
    border-radius: 0;
  }
}
.option-input--checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 21px;
  left: 13px;
  width: 23px;
  height: 16px;
  z-index: 1;
  background-image: url("../img/icon-tick.svg");
  background-position: center;
  background-repeat: no-repeat;
}
@media (min-width: 1200px) {
  .option-input--checkbox:checked + label::after {
    left: 18px;
  }
}
.option-input--radio:hover + label::before,
.option-input--checkbox:hover + label::before {
  background-color: #e5e5e5;
}
.option-input--radio:active + label::before,
.option-input--checkbox:active + label::before,
.option-input--radio:focus + label::before,
.option-input--checkbox:focus + label::before {
  background-color: #d5d5d5;
}
.option-input--radio:active + label::after,
.option-input--checkbox:active + label::after,
.option-input--radio:focus + label::after,
.option-input--checkbox:focus + label::after {
  opacity: 0.3;
}
