.text-input,
.text-input::placeholder {
  .title(16px; 26px);
  text-align: left;
  text-transform: none;
  opacity: 1;
}

.text-input {
  box-sizing: border-box;
  width: 100%;
  height: 58px;
  padding-left: 17px;
  padding-top: 5px;
  background-color: @input-gray;
  border: 4px solid transparent;

  &:hover {
    background-color: @medium-gray;
  }

  &:focus {
    background-color: @white;
    border: 4px solid @medium-gray;
  }
}

.option-input--radio + label,
.option-input--checkbox + label {
  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 0;
    width: 56px;
    height: 56px;
    background-color: @input-gray;
  }
}

.option-input--radio + label {
  &::before {
    border-radius: 50%;

    @media (min-width: @desktop-width) {
      left: -74px;
    }
  }
}

.option-input--radio:checked + label {
  &::after {
    content: "";
    position: absolute;
    top: 19px;
    left: 17px;
    width: 22px;
    height: 22px;
    background-color: @text-gray;
    border-radius: 50%;

    @media (min-width: @desktop-width) {
      left: -56px;
    }
  }
}

.option-input--checkbox + label {
  &::before {
    top: 1px;
    left: -1px;
    border-radius: 10%;

    @media (min-width: @tablet-width) {
      left: -5px;
    }

    @media (min-width: @desktop-width) {
      left: 1px;
      border-radius: 0;
    }
  }
}

.option-input--checkbox:checked + label {
  &::after {
    content: "";
    position: absolute;
    top: 21px;
    left: 13px;
    width: 23px;
    height: 16px;
    z-index: 1;
    background-image: url("../img/icon-tick.svg");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-width: @desktop-width) {
      left: 18px;
    }
  }
}

.option-input--radio:hover,
.option-input--checkbox:hover {
  + label::before {
    background-color: @medium-gray;
  }
}

.option-input--radio:active,
.option-input--checkbox:active,
.option-input--radio:focus,
.option-input--checkbox:focus {
  + label::before {
    background-color: @dark-gray;
  }

  + label::after {
    opacity: 0.3;
  }
}
